export class AjaxUtil {

    public static create(){
        var obj = new this();

        return obj;
    }

    public ajax(params) {
        params = params || {};

        params.data = params.data || {};

        params.jsonp ? this._jsonp(params) : this._json(params);

    }
    private _jsonp(params) {

        var callbackName = params.jsonp;

        var head = document.getElementsByTagName('head')[0];

        // 设置传递给后台的回调参数名

        params.data['jsoncallback'] = callbackName;

        var data = this._formatParams(params.data);

        var script = document.createElement('script');

        head.appendChild(script);

        //创建jsonp回调函数
        window[callbackName] = (json) => {

            head.removeChild(script);

            window[callbackName] = null;

            params.success && params.success(json);

        };
        script.src = params.url;
    }

    private _json(params) {

        //  请求方式，默认是GET

        params.type = (params.type || 'GET').toUpperCase();

        // 避免有特殊字符，必须格式化传输数据

        params.data = this._formatParams(params.data);

        var xhr = null;

        try {//IE系列浏览器
            xhr = new ActiveXObject("microsoft.xmlhttp");
        } catch (e1) {
            try {//非IE浏览器
                xhr = new XMLHttpRequest();
            } catch (e2) {
                console.log(xhr, {message: "您的浏览器不支持ajax，请更换！"});
                return null;
            }
        }


        // 监听事件，只要 readyState 的值变化，就会调用 readystatechange 事件

        xhr.onreadystatechange = function () {

            //  readyState属性表示请求/响应过程的当前活动阶段，4为完成，已经接收到全部响应数据

            if (xhr.readyState == 4) {

                var status = xhr.status;

                //  status：响应的HTTP状态码，以2开头的都是成功

                if (status >= 200 && status < 300) {

                    var response = '';

                    // 判断接受数据的内容类型

                    var type = xhr.getResponseHeader('Content-type');

                    if (type.indexOf('xml') !== -1 && xhr.responseXML) {

                        response = xhr.responseXML; //Document对象响应

                    } else if (type === 'application/json') {

                        response = JSON.parse(xhr.responseText); //JSON响应

                    } else {

                        response = xhr.responseText; //字符串响应

                    }
                    // 成功回调函数

                    params.success && params.success(response);

                } else {

                    params.error && params.error(status);

                }

            }
        };
        // 连接和传输数据

        if (params.type == 'GET') {

            // 三个参数：请求方式、请求地址(get方式时，传输数据是加在地址后的)、是否异步请求(同步请求的情况极少)；

            xhr.open(params.type, params.url + '?' + params.data, true);

            xhr.send(null);

        } else {

            xhr.open(params.type, params.url, true);

            //必须，设置提交时的内容类型

            xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded; charset=UTF-8');

            // 传输数据

            xhr.send(params.data);

        }
    }

    private _formatParams(data) {

        var arr = [];

        for (var name in data) {

            arr.push(encodeURIComponent(name) + '=' + encodeURIComponent(data[name]));

        }
        // 添加一个随机数，防止缓存

        arr.push('v=' + this._random());

        return arr.join('&');

    }

    private _random() {
        return Math.floor(Math.random() * 10000 + 500);
    }
}